import React, { Component } from "react";
import { Link } from "react-router-dom";
import { formatLongDate } from "../utils/FormatDates.js";
import { formatPublicLink } from "../utils/LinkUtils.js";

export class AllArticleItem extends Component {
  render() {
    return (
      <article className="border-t border-gray-200" data-aos="fade">
        <div className="py-2 md:py-6 flex">
          <div className="flex-1">
            <p className="font-sans text-gray-700 uppercase pt-4 mb-1 text-xs tracking-widest md:tracking-wider md:text-base">
              {this.props.data?.topic ? this.props.data.topic : "News Post"}
            </p>
            <Link to={this.props.data.path}>
              <span className="font-sans text-xl md:text-2xl font-medium mb-1 md:mb-3 link-subtle">
                {this.props.data?.title ? this.props.data.title : "Untitled Post"}
              </span>
            </Link>
            <p className="font-sans text-sm font-medium pt-2 pb-4 text-brownu-link">
              {this.props.data?.date
                ? formatLongDate(this.props.data.date)
                : null}
            </p>
          </div>
          <div className="hidden md:block relative bg-gray-400 overflow-hidden h-40 w-60 ml-4">
            <Link to={this.props.data.path} className="float-right absolute top-0 right-0 h-40 object-cover w-60">
              <img
                className="absolute h-full w-full object-cover"
                src={
                  this.props.data?.image?.url
                    ? formatPublicLink(this.props.data.image.url)
                    : `${process.env.PUBLIC_URL}/images/buo-1.jpg`
                }
                alt={
                  this.props.data?.image?.caption
                    ? this.props.data.image.caption
                    : ""
                }
              />
              <div className="border-2 border-white opacity-0 hover:opacity-60 absolute h-full w-full object-cover transform hover:scale-y-90 hover:scale-x-93 transition-all duration-200 ease-out"></div>
            </Link>
          </div>
        </div>
      </article>
    );
  }
}

export default AllArticleItem;
