export function formatLongDate(date) {
  let d = new Date(date);
  var nd = new Date(d.getTime() + Math.abs(d.getTimezoneOffset() * 60000));
  let month = nd.toLocaleString("default", { month: "long" });
  return month + " " + nd.getDate() + ", " + nd.getFullYear();
};

export function getOffsetDate(date) {
  let d = new Date(date);
  return new Date(d.getTime() + Math.abs(d.getTimezoneOffset() * 60000));
}