import React, { Component } from "react";
import { Redirect } from "react-router-dom";

const years = require("./past-seasons.json");
export class PastSeasonHome extends Component {
  render() {
    return (
      <>
        <div className="prose prose-content mx-8">
          <h1>Past Seasons</h1>
          <p>Browse through past seasons using the navigation.</p>
        </div>
        <Redirect
          to={
            "/events/historical/" +
            years[0] +
            "-" +
            (parseInt(years[0]) + 1)
          }
        />
      </>
    );
  }
}

export default PastSeasonHome;
