/*
  This file contains routes to all content. In some instances, we import
  markdown files (MDX), then wrap them in a prose element to introduce the
  correct margins before serving the content directly.
*/
/* eslint-disable import/no-webpack-loader-syntax */
import React, { Suspense } from "react";
import { Route, BrowserRouter as Router, Switch } from "react-router-dom";
import { ScrollToTop } from "./components/ScrollToTop";

import "./assets/main.css";
import "./assets/CircularStd.otf";

import Homepage from "./pages/Homepage";
import About from "./pages/About";
import PastSeasons from "./pages/past-seasons";
import UpcomingConcerts from "./pages/upcoming-concerts";
import News from "./pages/news";
import Media from "./pages/media";

import FourOhFour from "./pages/FourOhFour";

import Navbar from "./components/Navbar";
import Footer from "./components/Footer";

import MDTicketsFaq from "!babel-loader!mdx-loader!./markdown/TicketsFAQ.mdx";
import MDFaq from "!babel-loader!mdx-loader!./markdown/FAQ.mdx";
import MDPerforming from "!babel-loader!mdx-loader!./markdown/Performing.mdx";
import MDSupport from "!babel-loader!mdx-loader!./markdown/Support.mdx";
import MDAudition from "!babel-loader!mdx-loader!./markdown/Audition.mdx";
import MDTickets from "!babel-loader!mdx-loader!./markdown/Tickets.mdx";

import AOS from "aos";
import "aos/dist/aos.css";

import ReactGA from "react-ga4";
ReactGA.initialize("G-2W2J0KK8DD");
ReactGA.pageview(window.location.pathname + window.location.search);

/*
  TODO: Make sure to change this line based on where you
  are deploying it to!
*/
export const subdir = require("../package.json");

const Contact = React.lazy(() => import("./pages/Contact"));

const prose = (WrappedComponent) => {
  const fullComponent = class extends React.Component {
    render() {
      return (
        <div className="prose prose-content mx-auto py-8 lg:py-12 px-4">
          <WrappedComponent />
        </div>
      );
    }
  };
  return fullComponent;
};

function App() {
  React.useEffect(() => {
    AOS.init({
      duration: 500,
      easing: "ease-out-cubic",
      once: true,
      offset: 20,
    });
  }, []);
  return (
    <Router
      onUpdate={() => {
        window.scrollTo(0, 0);
        ReactGA.pageview(window.location.pathname + window.location.search);
      }}
      basename="/Departments/Music/sites/orchestra"
    >
      <Navbar />
      <ScrollToTop />
      <Switch>
        <Route exact path={`/`} component={Homepage} />
        <Route path={`/about`} component={About} />
        <Route exact path={`/events`} component={UpcomingConcerts} />
        <Route path={`/events/historical`} component={PastSeasons} />
        <Route exact path={`/events/tickets`} component={prose(MDTickets)} />
        <Route path={`/news`} component={News} />
        <Route
          exact
          path={`/contact`}
          render={() => (
            <Suspense fallback={<div className="min-h-screen"></div>}>
              <Contact />
            </Suspense>
          )}
        />
        <Route exact path={`/faq`} component={prose(MDTicketsFaq)} />
        <Route exact path={`/audition/faq`} component={prose(MDFaq)} />
        <Route exact path={`/performing`} component={prose(MDPerforming)} />
        <Route path={`/media`} component={Media} />
        <Route exact path={`/audition`} component={prose(MDAudition)} />
        <Route exact path={`/support`} component={prose(MDSupport)} />
        <Route component={FourOhFour} />
      </Switch>
      <Footer />
    </Router>
  );
}

export default App;
