import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";
/* eslint-disable import/no-webpack-loader-syntax */

import FourOhFour from "../FourOhFour";
import PastInfo from "./PastSeasonFormat";

import PastSeasonHome from "./PastSeasonHome"
import PageSidebar from "../../components/PageSidebar";

const years = require("./past-seasons.json");

const sidebar = years.map(year => {
  let dates = year + "-" + (parseInt(year) + 1);
  return {
    name: dates,
    href: "/events/historical/" + dates,
  };
})

export class PastSeasons extends Component {
  render() {
    return (
      <div className="container mx-auto">
        <div className="grid lg:grid-cols-5 gap-8 gap-y-0 pt-0 lg:pt-8 py-8 lg:py-12">
          <PageSidebar links={sidebar} />
          <div className="col-span-4 px-2 md:px-4 lg:px-8">
            <Switch>
              {years.map((year) => {
                return (
                  <Route
                    exact
                    path={`/events/historical/${year}-${parseInt(year) + 1}`}
                    component={() => <PastInfo date={year}></PastInfo>}
                    key={year}
                  />
                );
              })}
              <Route
                exact
                path={`/events/historical`}
                component={PastSeasonHome}
              />
              <Route component={FourOhFour} />
            </Switch>
          </div>
        </div>
      </div>
    );
  }
}

export default PastSeasons;
