/*
  This component should mirror LatestNewsCards - it's used on the bottom of news articles but only three will show up instead of four, hence the name.
*/
import React, { Component } from "react";
import { getOffsetDate } from "../utils/FormatDates";
import NewsCard from "./NewsCard";

const articles = require("../pages/news/articles.json");

export class LatestNewsCards extends Component {
  render() {
    return (
      <React.Fragment>
        <div className="grid-flow-row hidden lg:grid lg:grid-cols-3 gap-4">
          {articles.slice(0, 3).map((article, i) => {
            // TODO: If four articles don't exist, make it not fail
            let doo;
            if (article.date) {
              var d = getOffsetDate(article.date);
              let month = d.toLocaleString("default", { month: "short" });
              doo = month + " " + d.getDate();
            }
            return (
              <NewsCard
                key={i}
                topic={article.topic}
                title={article.title}
                to={article.path}
                date={doo}
                image={article.image}
              />
            );
          })}
        </div>
        <div className="grid grid-flow-row grid-cols-2 lg:hidden gap-4">
          {articles.slice(0, 4).map((article, i) => {
            // TODO: If four articles don't exist, make it not fail
            let doo;
            if (article.date) {
              var d = getOffsetDate(article.date);
              let month = d.toLocaleString("default", { month: "short" });
              doo = month + " " + d.getDate();
            }
            return (
              <NewsCard
                key={i}
                topic={article.topic}
                title={article.title}
                to={article.path}
                date={doo}
                image={article.image}
              />
            );
          })}
        </div>
      </React.Fragment>
    );
  }
}

export default LatestNewsCards;
