import React, { Component } from "react";
import AllArticleItem from "./AllArticleItem";
import { formatLongDate } from "../utils/FormatDates.js";

const articles = require("../pages/news/articles.json");
const years = articles
  .map((obj) => obj.date.slice(0, 4))
  .filter((item, i, ar) => ar.indexOf(item) === i)
  .sort()
  .reverse();

export class AllArticles extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchvalue: "",
      articles: [],
    };
    this.handleChange = this.handleChange.bind(this);
  }
  componentDidMount() {
    this.search(this.props.search)
  }
  handleChange(e) {
    this.search(e.target.value);
  }
  search(value) {
    this.setState({
      searchvalue: value,
      articles: getArticles(value),
    });
  }
  render() {
    return (
      <div className="lg:grid lg:grid-cols-5 lg:gap-8">
        <div className="col-span-1 lg:border-r border-grey-200">
          <div className="lg:mr-8">
            <p className="font-sans text-gray-700 uppercase mb-2 tracking-wider">
              Search
            </p>
            <div className="relative flex items-center lg:max-w-xs mb-2">
              <input
                type="text"
                className="form-input w-full text-gray-800 px-3 py-2 pr-12 text-sm border rounded focus:outline-none focus:ring-1 ring-gray-300"
                placeholder="Search news articles"
                value={this.state.searchvalue}
                onChange={this.handleChange}
                required
              />
              <button
                type="submit"
                className="absolute inset-0 left-auto focus:outline-none"
                aria-label="Search"
              >
                <span
                  className="absolute inset-0 right-auto w-px -ml-px my-2 bg-gray-300"
                  aria-hidden="true"
                ></span>
                <svg
                  className="w-4 h-4 fill-current text-blue-600 mx-2 flex-shrink-0"
                  viewBox="2 2 22 21"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path d="M0 0h24v24H0z" fill="none" />
                  <path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z" />
                </svg>
              </button>
            </div>
            
              <div onClick={() => this.search("")} className={"font-sans text-brownu-brown hover:text-brownu-link text-sm py-3 px-2 border-t border-gray-200 block hover:bg-gray-100 duration-100 cursor-pointer"+(this.state.searchvalue === "" || !this.state.searchvalue ? " text-brownu-link font-medium" : "")}>
                All Articles
              </div>
            
            {years.map((year, i) => {
              return (
                <div
                  id={year.toString()+i}
                  onClick={() => this.search(year)}
                  className={"font-sans text-brownu-brown hover:text-brownu-link text-sm py-3 px-2 border-t border-gray-200 block hover:bg-gray-100 duration-100 cursor-pointer"+(this.state.searchvalue === year ? " text-brownu-link font-medium" : "")}
                >
                  {year}
                </div>
              );
            })}
          </div>
        </div>
        <div className="col-span-4">
          <p className="py-4 font-sans border-b-4 border-gray-200 text-gray-800">
            <span className="font-medium text-black text-sm md:text-base">
              {this.state.articles.length}
            </span>{" "}
            result{this.state.articles.length !== 1 ? "s" : null} returned from
            your query.
          </p>
          {this.state.articles.map((article, i) => {
            return <AllArticleItem key={i} data={article} />;
          })}
        </div>
      </div>
    );
  }
}
function getArticles(searchTerm) {
  return searchTerm && searchTerm !== ""
    ? articles.filter(
        (article) =>
          article.date.includes(searchTerm) ||
          article.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
          article.author.name
            .toLowerCase()
            .includes(searchTerm.toLowerCase()) ||
          article.topic
            .toLowerCase()
            .includes(searchTerm.toLowerCase()) ||
          formatLongDate(article.date)
            .toLowerCase()
            .includes(searchTerm.toLowerCase())
      )
    : articles;
}
export default AllArticles;
