import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";
/* eslint-disable import/no-webpack-loader-syntax */

import FourOhFour from "../pages/FourOhFour";

import MDAbout from "!babel-loader!mdx-loader!../markdown/About.mdx";
import MDAboutHistory from "!babel-loader!mdx-loader!../markdown/AboutHistory.mdx";
import MDAboutConductor from "!babel-loader!mdx-loader!../markdown/AboutConductor.mdx";

import Members from "../pages/Members";

import AboutSidebar from "../components/PageSidebar";

const sidebarlinks = [
  {
    name: "About the Orchestra",
    href: "/about",
  },
  {
    name: "History",
    href: "/about/history",
  },
  {
    name: "Members",
    href: "/about/members",
  },
  {
    name: "About Maestro Seto",
    href: "/about/conductor",
  },
];

const prose = (WrappedComponent) => {
  const fullComponent = class extends Component {
    render() {
      return (
        <div className="prose prose-content mx-auto">
          <WrappedComponent />
        </div>
      );
    }
  };
  return fullComponent;
};

export class About extends Component {
  render() {
    return (
      <div className="container mx-auto">
        <div className="grid lg:grid-cols-5 gap-8 gap-y-0 pt-0 lg:pt-8 py-8 lg:py-12">
          <AboutSidebar links={sidebarlinks} sticky={true} />
          <div className="col-span-4 px-4 lg:px-8">
            <Switch>
              <Route
                exact
                path={`/about`}
                component={prose(MDAbout)}
              />
              <Route
                exact
                path={`/about/history`}
                component={prose(MDAboutHistory)}
              />
              <Route
                exact
                path={`/about/members`}
                component={Members}
              />
              <Route
                exact
                path={`/about/conductor`}
                component={prose(MDAboutConductor)}
              />
              <Route component={FourOhFour} />
            </Switch>
          </div>
        </div>
      </div>
    );
  }
}

export default About;
