export function formatItalics(text) {
  let formatted = [];
  var regex = /\*.*?\*|.*?(?=\*)|(?!\*).*/gi;
  let match = text.match(regex);
  match.forEach((words, wordsi) => {
    if (words.substr(0, 1) === "*") {
      formatted.push(
        <span key={"words" + wordsi} className="inline italic">
          {words.substr(1, words.length - 2)}
        </span>
      );
    } else if (words !== "") {
      formatted.push(
        <span key={"words" + wordsi} className="inline">
          {words}
        </span>
      );
    }
  });
  return formatted;
}
