import React, { Component } from "react";
import { ControlledLink, formatPublicLink } from "../utils/LinkUtils";

export class HeroSlider extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imageIndex: 0,
      repeat: "",
    };
  }

  componentDidMount() {
    let repeat = setTimeout(() => {
      this.plusSlides(1);
    }, 5000);
    this.setState({
      repeat: repeat,
    });
  }

  componentWillUnmount() {
    clearInterval(this.state.repeat);
  }

  render() {
    return (
      <article className="md:rounded-lg overflow-hidden mb-4">
        <div className="relative bg-gray-400 lg:pb-1/3 sm:pb-1/2 pb-96">
          <div className="absolute h-full w-full">
            {this.props.data.map((data, i) => {
              return (
                <img
                  className={
                    "absolute h-full w-full object-cover transition-opacity" +
                    (this.state.imageIndex === i
                      ? " duration-500 opacity-100 z-10"
                      : " z-0 delay-500 opacity-0")
                  }
                  src={formatPublicLink(data.imagesrc)}
                  alt={data.alt}
                  key={i}
                />
              );
            })}
          </div>
          <div className="absolute z-20 h-full w-full">
            <div className="absolute bottom-0 w-full h-1/2 md:h-1/3 bg-gradient-to-t from-black mx-auto"></div>
            <div className="absolute flex items-end bottom-0 left-0 right-0 mx-8 md:mx-10 mb-6 md:mb-10">
              <div className="flex-grow">
                <h2 className="font-semibold md:text-2xl text-xl text-white mb-1">
                  {this.props.data[this.state.imageIndex].link ? (
                    <ControlledLink
                      href={this.props.data[this.state.imageIndex].link}
                      className="link"
                    >
                      {this.props.data[this.state.imageIndex].title}
                    </ControlledLink>
                  ) : (
                    this.props.data[this.state.imageIndex].title
                  )}
                </h2>
                <p className="md:text-lg text-base text-white">
                  {this.props.data[this.state.imageIndex].subtitle}
                </p>
              </div>
              {this.props.data[this.state.imageIndex].logo ? (
                <div className="flex-none p-2 -mr-2 -mb-2">
                  <img
                    className="h-24 md:h-32"
                    src={process.env.PUBLIC_URL + "/images/buo-logo.jpg"}
                    alt="The Brown University Orchestra"
                  />
                </div>
              ) : null}
            </div>

            <div className="btn-wrapper">
              <button
                className="text-6xl text-white text-center absolute inset-y-0 left-0 bg-gradient-to-r from-black opacity-75 transition-opacity 
  							duration-200 hover:opacity-100 focus:outline-none"
                onClick={(e) => this.plusSlides(-1, e)}
                aria-label="Left Slider Button"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 24 24"
                  fill="white"
                  width="36px"
                  height="36px"
                >
                  <path d="M0 0h24v24H0V0z" fill="none" />
                  <path d="M15.41 16.59L10.83 12l4.58-4.59L14 6l-6 6 6 6 1.41-1.41z" />
                </svg>
              </button>
              <button
                className="text-6xl text-white text-center absolute inset-y-0 right-0 bg-gradient-to-l from-black opacity-75 transition-opacity 
  							duration-200 hover:opacity-100 focus:outline-none"
                onClick={(e) => this.plusSlides(1, e)}
                aria-label="Right Slider Button"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 24 24"
                  fill="white"
                  width="36px"
                  height="36px"
                >
                  <path d="M0 0h24v24H0V0z" fill="none" />
                  <path d="M8.59 16.59L13.17 12 8.59 7.41 10 6l6 6-6 6-1.41-1.41z" />
                </svg>
              </button>
            </div>
          </div>
          <div className="absolute bottom-0 mb-2 z-20 w-full text-center">
            <div className="relative">
              {this.props.data.map((data, i) => {
                return (
                  <span
                    className={
                      "border border-white rounded-full inline-block h-3 w-3 m-1 transition-colors text-0" +
                      (this.state.imageIndex === i
                        ? " bg-white"
                        : " bg-transparent cursor-pointer")
                    }
                    onClick={(e) => this.setSlide(i)}
                    key={i}
                  >
                    {i}
                  </span>
                );
              })}
            </div>
          </div>
        </div>
      </article>
    );
  }

  resetSlideshow = () => {
    this.setState({ repeat: clearInterval(this.state.repeat) });
    let repeat = setInterval(() => {
      this.plusSlides(1);
    }, 8000);
    this.setState({ repeat: repeat });
  };

  setSlide = (n) => {
    this.setState({ imageIndex: n });
    this.resetSlideshow();
  };

  plusSlides = (n) => {
    var newIndex = this.state.imageIndex + n;
    if (newIndex < 0) {
      this.setState({ imageIndex: this.props.data.length - 1 });
    } else if (newIndex >= this.props.data.length) {
      this.setState({ imageIndex: 0 });
    } else {
      this.setState({ imageIndex: newIndex });
    }
    this.resetSlideshow();
  };
}

export default HeroSlider;
