export class index extends Component {
  constructor(state) {
    super(state);
    this.state = { modaldata: null };
  }
  openProgram = (index) => {
    this.setState({ modaldata: data[index] });
  };
  isLink = (link) => {
    if (
      link?.includes("://") // TODO: Make this check more comprehensive
    ) {
      return true;
    }
    return false;
  };
  render() {
    return (
      <>
        <div className="container mx-auto my-8">
          <div className="grid md:grid-cols-5 md:gap-4 mx-2 md:mx-0">
            <div className="prose prose-content col-start-2 col-span-3 mb-4">
              <h1>Current Season</h1>
            </div>
            {data.map((event, eventindex) => {
              return (
                <React.Fragment key={"page" + eventindex}>
                  <div className="lg:flex border border-gray-200 col-span-3 col-start-2 rounded-md overflow-hidden my-2 md:my-0">
                    <div className="lg:flex-shink-0">
                      <img
                        src={`${process.env.PUBLIC_URL}${event.thumbnail}`}
                        alt="BUO"
                        className="h-48 w-full lg:w-48 lg:h-full object-cover"
                      />
                    </div>
                    <div className="p-4 lg:flex-1">
                      <div className="text-lg font-medium">{event.title}</div>
                      {event.date ? (
                        <div className="mb-4">
                          {new Date(event.date).toLocaleString("default", {
                            month: "long",
                            weekday: "long",
                            day: "numeric",
                            hour: "numeric",
                            minute: "2-digit",
                            year: "numeric",
                            hour12: true,
                          })}
                        </div>
                      ) : (
                        <div className="mb-4 italic text-gray-400">
                          Date TBA
                        </div>
                      )}
                      <div className="mb-4">{event.description}</div>
                      <div className="flex items-center">
                        {event.program ? (
                          <div className="flex-1">
                            <div
                              className="link inline-block"
                              onClick={() => this.openProgram(eventindex)}
                            >
                              View Concert Program
                            </div>
                          </div>
                        ) : (
                          <div className="flex-1" />
                        )}
                        {event.button?.src && this.isLink(event.button.src) ? (
                          <a
                            href={event.button.src}
                            className="bg-black hover:bg-brownu-link text-white p-3 transition-all duration-100 cursor-pointer float-right"
                          >
                            {event.button?.title
                              ? event.button.title
                              : "Reserve Tickets"}
                          </a>
                        ) : event.button?.title ? (
                          <div className="bg-gray-400 text-white p-3 cursor-not-allowed float-right">
                            {event.button.title}
                          </div>
                        ) : null}
                      </div>
                    </div>
                  </div>
                </React.Fragment>
              );
            })}
          </div>
        </div>
        {this.state.modaldata ? (
          <div
            className="fixed top-0 left-0 h-full w-full cursor-pointer z-40"
            style={{ backgroundColor: "rgba(0, 0, 0, 0.5)" }}
            onClick={(e) => {
              if (e.target.id === "closemodal")
                this.setState({ modaldata: null });
            }}
          >
            <div id="closemodal" className="flex h-full w-full justify-center">
              <div className="m-auto bg-white center cursor-default p-4 rounded-md">
                <p className="text-3xl font-bold mb-2">Program</p>
                <Program meta={{ concerts: [this.state.modaldata.program] }} />
                <div
                  className="bg-black hover:bg-brownu-link text-white inline-block px-3 py-2 transition-all duration-100 cursor-pointer float-right mt-2"
                  onClick={() => this.setState({ modaldata: null })}
                >
                  Close
                </div>
              </div>
            </div>
          </div>
        ) : null}
      </>
    );
  }
}

export default index;
