import React, { Component } from "react";
import { Link } from "react-router-dom";
import { formatPublicLink } from "../utils/LinkUtils";

export class NewsCard extends Component {
  render() {
    return (
      <article>
        <Link to={this.props.to ? this.props.to : "/news"} aria-label={this.props.title ? "Link to " + this.props.title : "Link to Untitled Post"}>
          <div className="relative bg-gray-400 pb-2/3 overflow-hidden">
            <img
              className="absolute h-full w-full object-cover"
              src={
                this.props.image?.url
                  ? formatPublicLink(this.props.image.url)
                  : `${process.env.PUBLIC_URL}/images/buo-1.jpg`
              }
              alt={this.props.image?.caption ? this.props.image.caption : ""}
            />
            <div className="border-2 border-white opacity-0 hover:opacity-60 absolute h-full w-full object-cover transform hover:scale-y-90 hover:scale-x-93 transition-all duration-200 ease-out"></div>
          </div>
        </Link>
        <div>
          <div className="relative py-3 sm:py-2 md:mt-2 px-3 sm:px-1">
            <p className="uppercase text-xs mb-1 sm:mb-0 sm:text-sm text-gray-600 tracking-wider">
              {this.props.topic ? this.props.topic : "News Post"}
              {this.props.date ? " ∙ " + this.props.date : null}
            </p>
            <Link to={this.props.to ? this.props.to : "/news"}>
              <span className="font-medium text-lg tracking-wide link-subtle">
                {this.props.title ? this.props.title : "Untitled Post"}
              </span>
            </Link>
          </div>
        </div>
      </article>
    );
  }
}

export default NewsCard;
