/* A lot of code was taken out of here: https://auth0.com/blog/how-to-build-an-app-with-react-and-mdx/
   Huge shoutout to those devs for saving me time here - really appreciate it!
   All this extra code helps dynamically render MDX content. Not sure if it decreases bundle size (probs no)
*/

import React, { Component } from "react";

import FourOhFour from "../FourOhFour";
import { formatItalics } from "../../utils/FormatItalics";

export class MediaFormat extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Component: "loading",
      meta: "",
      page: this.props
        .date /* A lot of the manipulation you see is to get URLs in the format of {root}/news/date/article */,
    };
  }

  async addComponent(name) {
    import(`./media-info/${name}.json`)
      .then((data) =>
        this.setState({
          Component: "loadTheData", // TODO: Fix this - it's nigh useless
          meta: data, // Don't be confused by the key "meta" - it's just data from the JSON file that used to be stored as meta in a MDX file
        })
      )
      .catch(() => {
        this.setState({
          Component: "404",
          meta: "",
        });
      });
  }

  async componentDidMount() {
    await this.addComponent(this.state.page);
  }

  async componentDidUpdate(prevProps, prevState) {
    if (prevState.page !== this.state.page) {
      await this.addComponent(this.state.page);
    }
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.date !== prevState.page) {
      return {
        page: nextProps.date,
      };
    } else return null;
  }

  render() {
    const { Component, meta } = this.state;
    return (
      <React.Fragment>
        {Component === "loading" ? (
          <div className="min-h-screen" />
        ) : Component === "404" ? (
          <FourOhFour />
        ) : Component ? (
          <Page mdx={Component} meta={meta} />
        ) : (
          <FourOhFour />
        )}
      </React.Fragment>
    );
  }
}

function Page(state) {
  const meta = state.meta;
  return (
    <div
      className="container mx-auto px-0 md:px-8 max-w-4xl"
      data-aos="fade" data-aos-anchor="body"
    >
      <div className="prose prose-content ml-4 md:ml-0">
        {" "}
        {/* Margin is to bump title away from edge on mobile */}
        <h1>{meta.year}</h1>

      </div>
      <MediaPage videos={meta.videos} />
    </div>
  );



}
function formatDate(date) {
  //We probably should've used moment.js...
  const months = ["January", "February", "March", "April", "May", "June",
    "July", "August", "September", "October", "November", "December"
  ];

  const days = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"]

  let d = new Date(date);

  let day = d.getUTCDate().toString();
  if (day.length === 1)
    day = "0" + day

  return days[d.getUTCDay()] + ", " + months[d.getUTCMonth()] + " " + day + ", " + d.getUTCFullYear()
}

export class MediaPage extends Component {
  render() {

    let totalVideos = []
    if (typeof (this.props.videos) !== 'undefined') {
      this.props.videos.forEach((video) => {
        totalVideos.push(
          <div className="mt-4 mb-20" key={video.link}>
            <p
              className="text-2xl font-serif"
              style={{ marginTop: "-20px", marginBottom: "1rem" }}
            >
              {formatDate(video.date)}
            </p>
            <h2 className="mb-2">{formatItalics(video.title)}</h2>
            <div className="aspect-w-16 aspect-h-9">
              <iframe allow="fullscreen" title={video.link}
                src={video.link} />
            </div>
          </div>)
      })
    }
    return (
      <div>
        { totalVideos.map((display) => { return display })}
      </div>
    )
  }


}

export default MediaFormat;
