/* A lot of code was taken out of here: https://auth0.com/blog/how-to-build-an-app-with-react-and-mdx/
   Huge shoutout to those devs for saving me time here - really appreciate it!
   All this extra code helps dynamically render MDX content. Not sure if it decreases bundle size (probs no)
*/

import React, { Component } from "react";

//import NewsPosts from "brown-orchestra-blog"

//import ArticleSkeleton from "../../components/ArticleSkeleton"
import FourOhFour from "../../pages/FourOhFour";

import LatestNewsCards3 from "../../components/LatestNewsCards3";

import { formatLongDate } from "../../utils/FormatDates";
import { formatPublicLink } from "../../utils/LinkUtils";
import { Link } from "react-router-dom";

export class BlogFormat extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Component: "loading",
      meta: "",
      readingTime: "",
      page:
        this.props.match.params.date +
        "-" +
        this.props.match.params
          .name /* A lot of the manipulation you see is to get URLs in the format of {root}/news/date/article */,
    };
  }

  async addComponent(name) {
    import(`!babel-loader!mdx-loader!./posts/${name}.mdx`)
      .then((component) =>
        this.setState({
          Component: component.default,
          meta: component.meta,
          readingTime: component.readingTime,
        })
      )
      .catch(() => {
        this.setState({
          Component: "404",
          meta: "",
          readingTime: "",
        });
      });
  }

  async componentDidMount() {
    await this.addComponent(this.state.page);
  }

  async componentDidUpdate(prevProps, prevState) {
    if (prevState.page !== this.state.page) {
      await this.addComponent(this.state.page);
    }
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (
      nextProps.match.params.date + "-" + nextProps.match.params.name !==
      prevState.page
    ) {
      return {
        page: nextProps.match.params.date + "-" + nextProps.match.params.name,
      };
    } else return null;
  }

  render() {
    const { Component, meta, readingTime } = this.state;
    return (
      <React.Fragment>
        {Component === "loading" ? (
          /*<ArticleSkeleton />*/ <div className="min-h-screen" />
        ) : Component === "404" ? (
          <FourOhFour />
        ) : Component ? (
          <Page mdx={Component} meta={meta} readingTime={readingTime} />
        ) : (
          <FourOhFour />
        )}
      </React.Fragment>
    );
  }
}

function Page(props) {
  const MDX = props.mdx;
  const meta = props.meta;
  //const readingTime = props.readingTime;
  return (
    <div className="container mx-auto">
      <div className="lg:grid lg:grid-cols-5 gap-8 gap-y-0 py-8 lg:py-12">
        <div className="col-span-1 pl-4">
          <div className="sticky top-0 lg:pt-4 sm:grid lg:block grid-rows-3 sm:grid-cols-2 sm:grid-rows-none lg:grid-cols-none lg:grid-rows-3 grid-flow-col sm:grid-flow-row lg:grid-flow-col">
            {/* I have no words for how ugly this is. If you can make it nicer and as responsive, be my guest. */}
            <div>
              <p className="font-sans text-gray-700 uppercase text-sm pt-4 border-t border-gray-200">
                Topics
              </p>
              <p className="font-serif text-lg mt-1 mb-4">{meta.topic}</p>
            </div>
            <div>
              <p className="font-sans text-gray-700 uppercase text-sm pt-4 border-t border-gray-200">
                Date
              </p>
              <p className="font-serif text-lg mt-1 mb-4">
                {formatLongDate(meta.date)}
              </p>
            </div>
            <div>
              <p className="font-sans text-gray-700 uppercase text-sm pt-4 border-t border-gray-200">
                Contact
              </p>
              <p className="font-serif text-lg mt-1 mb-4 pb-4">
                {meta.author.name}
                <br />
                <a
                  href="mailto:isaac_y_kim@brown.edu"
                  className="border-b-2 border-black hover:border-gray-600 hover:text-gray-600 transition-all duration-200"
                >
                  {meta.author.email}
                </a>
              </p>
            </div>
            <div>
              <div className="pt-4 mb-4 border-t border-gray-200">
                <Link
                  className="text-brownu-link uppercase text-sm font-medium tracking-wider pt-4"
                  to="/news"
                >
                  ← All Articles
                </Link>
              </div>
            </div>
          </div>
        </div>
        <div className="col-span-4 lg:border-l lg:border-gray-200 px-4 lg:px-8">
          <div className="prose prose-content mx-auto mb-4">
            <h1 className="text-4xl md:text-5xl font-bold">{meta.title}</h1>
            <p
              className="text-2xl font-serif"
              style={{ marginTop: "-20px", marginBottom: "1rem" }}
            >
              {meta.subtitle}
            </p>
            {meta.image?.url ? (
              <img
                src={formatPublicLink(meta.image.url)}
                alt={meta.image?.caption ? meta.image.caption : ""}
              />
            ) : (
              <img
                src={`${process.env.PUBLIC_URL}/images/buo-1.jpg`}
                alt="The Brown University Orchestra"
              />
            )}
            {meta.image?.caption ? (
              <h6 className="font-serif">
                {meta.image.caption}
                {meta.image?.attr ? <em className="text-gray-400"> {" " + meta.image.attr}</em> : null}
              </h6>
            ) : null}
          </div>
          <div className="prose prose-content mx-auto">
            {MDX ? <MDX /> : null}
          </div>
          <div className="max-w-4xl mx-auto mt-12 border-t border-grey-200">
            <p className="text-2xl sm:text-3xl font-semibold font-serif pb-4 pt-4">
              Other Recent News
            </p>
            <LatestNewsCards3 />
          </div>
        </div>
      </div>
    </div>
  );
}

export default BlogFormat;
