import React, { Component } from "react";
import { Link } from "react-router-dom";

const members = require("./members.json");
const column1 = members.column1
const column2 = members.column2
const eboard = require("./eboard.json")

function Section(props) {
  return (
    <div>
      <h3 className="text-sm font-semibold text-gray-800 uppercase font-sans tracking-wider mt-6 mb-2">
        {props.name}
      </h3>
      {props.data.map((item, index) => {
        return <SectionMembers item={item} key={index} />;
      })}
    </div>
  );
}

function SectionMembers(props) {
  return <div className="border-t py-2 pl-4 text-gray-900">{props.item}</div>;
}

function fireworks() {
  function randomInRange(min, max) {
    return Math.random() * (max - min) + min;
  }
  var defaults = { startVelocity: 30, spread: 360, ticks: 60, zIndex: 0 };
  window.confetti(
    Object.assign({}, defaults, {
      particleCount: 50,
      origin: { x: randomInRange(0.1, 0.3), y: Math.random() - 0.2 },
    })
  );
  window.confetti(
    Object.assign({}, defaults, {
      particleCount: 50,
      origin: { x: randomInRange(0.7, 0.9), y: Math.random() - 0.2 },
    })
  );
}

function Eboard(props) {
  return Object.keys(eboard).map((position) => {
    return (
      <div key={position}>
        <h3 className="text-sm font-semibold text-gray-800 uppercase font-sans tracking-wider mt-4">
          {position}
        </h3>
        {eboard[position].map((name) => {
          return <EboardMember name={name} key={name} />;
        })}
      </div>
    );
  });
}

function EboardMember(props) {
  if (props.name === "Isaac Kim") {
    // Fires confetti when clicking my name! Minimal impact: bundle is 15kb
    return (
      <p className="py-1 cursor-pointer" onClick={() => fireworks()}>
        {props.name}
      </p>
    );
  } else {
    return <p className="py-1">{props.name}</p>;
  }
}

export class Members extends Component {
  render() {
    return (
      <React.Fragment>
        <h1 className="text-4xl font-serif font-semibold leading-tight">
          The Brown University Orchestra Roster
        </h1>
        <h2 className="text-2xl font-serif font-semibold">{members.title ? members.title : null}</h2>
        <div className="grid grid-cols-6 md:grid-cols-8 gap-4">
          <div className="col-span-3">
            {Object.keys(column1).map((section) => {
              return (
                <Section name={section} key={section} data={column1[section]} />
              );
            })}
          </div>
          <div className="col-span-3">
            {Object.keys(column2).map((section) => {
              return (
                <Section name={section} key={section} data={column2[section]} />
              );
            })}
          </div>
          <div className="col-span-6 text-center md:text-left md:col-span-2 md:border-l pl-4">
            <h3 className="text-md font-bold text-gray-800 uppercase font-sans tracking-wider mt-6 mb-2">
              Conductor
            </h3>
            <Link
              to="/about/conductor"
              className="underline text-brownu-brown hover:text-brownu-link"
            >
              Mark Seto
            </Link>
            <h3 className="text-md font-bold text-gray-800 uppercase font-sans tracking-wider mt-6 mb-4">
              Executive Board
            </h3>
            <Eboard />
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Members;
