import React, { Component } from "react";
import { Link, NavLink } from "react-router-dom";

export function formatPublicLink(url) {
  if (
    url &&
    !url.includes("http://") &&
    !url.includes("https://") &&
    url.charAt(0) === "/"
  ) {
    return process.env.PUBLIC_URL + url;
  } else {
    return url;
  }
}

export function isExternalLink(url) {
  if (
    url &&
    !url.includes("http://") &&
    !url.includes("https://") &&
    url.charAt(0) === "/"
  ) {
    return false;
  } else if (url === "") {
    return null;
  }
  return true;
}

export class ControlledLink extends Component {
  // this may need to be in components, but I'm leaving it here for the forseeable future
  render() {
    let ext = isExternalLink(this.props.href);
    if (ext) {
      return (
        <a href={this.props.href} className={this.props.className}>
          {this.props.children}
        </a>
      );
    } else if (ext === null) {
      <div className={this.props.className}>{this.props.children}</div>;
    } else if (this.props.navlink) {
      return (
        <NavLink
          exact
          to={this.props.href}
          className={this.props.className}
          activeClassName={this.props.activeClassName}
        >
          {this.props.children}
        </NavLink>
      );
    }
    return (
      <Link
        to={this.props.href}
        className={this.props.className}
      >
        {this.props.children}
      </Link>
    );
  }
}
