import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";

import NewsHome from "./NewsHome.js";
import NewsFormat from "./NewsFormat";

export class index extends Component {
  render() {
    return (
      <div>
        <Switch>
          {/* A lot of the manipulation you see is to get URLs in the format of {root}/news/date/article */}
          <Route
            exact
            path={`/news/:date/:name`}
            render={(props) => <NewsFormat {...props} />}
          />
          <Route
            path={`/news/:search?`}
            render={(props) => <NewsHome {...props} />}
          />
        </Switch>
      </div>
    );
  }
}

export default index;
