import React, { Component } from "react";
import { ControlledLink } from "../utils/LinkUtils";

export class Dropdown extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: true,
    };
  }
  /*
  The below function is called when you click on an item in the desktop
  dropdown - it then hides the element for a second to remove the dropdown
  as I couldn't figure out a more elegant solution - I'm on an airplane
  and can't really google anything right now. Feel free to fix, but this
  is a completely acceptable solution.
  */
  toggleVisiblity = () => {
    this.setState({ visible: false });
    setTimeout(() => {
      this.setState({ visible: true });
    }, 1000);
  };
  render() {
    return (
      <div
        className={
          "absolute py-2 w-48 bg-white rounded-md shadow-xl z-30 transition-opacity duration-100 border border-gray-200 opacity-0 pointer-events-none group-hover:opacity-100 group-hover:pointer-events-auto focus-within:opacity-100 focus-within:pointer-events-auto" +
          (!this.state.visible
            ? " hidden"
            : "") /* +(this.props.visible ? " opacity-100" : " opacity-0 pointer-events-none") */
        }
        onClick={() => {
          this.toggleVisiblity();
        }}
      >
        {/* pointer-events-none is to prevent interaction with element while invisible. */}
        {Object.keys(this.props.items).map((name, index) => {
          return (
            <ControlledLink
              key={"dropdown" + index}
              href={this.props.items[name]}
              className="block px-4 py-2 text-sm capitalize text-gray-700 hover:text-brownu-link hover:bg-gray-50"
            >
              {name}
            </ControlledLink>
          ); //<DropdownItem key={index} title={name} to={this.props.items[name]} />;
        })}
      </div>
    );
  }
}

export default Dropdown;
