/* A lot of code was taken out of here: https://auth0.com/blog/how-to-build-an-app-with-react-and-mdx/
   Huge shoutout to those devs for saving me time here - really appreciate it!
   All this extra code helps dynamically render MDX content. Not sure if it decreases bundle size (probs no)
*/

import React, { Component } from "react";

import FourOhFour from "../FourOhFour";
import { formatItalics } from "../../utils/FormatItalics";


export class PastSeasonFormat extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Component: "loading",
      meta: "",
      page: this.props
        .date /* A lot of the manipulation you see is to get URLs in the format of {root}/news/date/article */,
    };
  }

  async addComponent(name) {
    import(`./seasons/${name}.json`)
      .then((data) =>
        this.setState({
          Component: "loadTheData", // TODO: Fix this - it's nigh useless
          meta: data, // Don't be confused by the key "meta" - it's just data from the JSON file that used to be stored as meta in a MDX file
        })
      )
      .catch(() => {
        this.setState({
          Component: "404",
          meta: "",
        });
      });
  }

  async componentDidMount() {
    await this.addComponent(this.state.page);
  }

  async componentDidUpdate(prevProps, prevState) {
    if (prevState.page !== this.state.page) {
      await this.addComponent(this.state.page);
    }
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.date !== prevState.page) {
      return {
        page: nextProps.date,
      };
    } else return null;
  }

  render() {
    const { Component, meta } = this.state;
    return (
      <React.Fragment>
        {Component === "loading" ? (
          <div className="min-h-screen" />
        ) : Component === "404" ? (
          <FourOhFour />
        ) : Component ? (
          <Page mdx={Component} meta={meta} />
        ) : (
          <FourOhFour />
        )}
      </React.Fragment>
    );
  }
}

function getDateLocation(concert) {
  let formattedDates = [];
  if (concert.locations.length === 1) {
    concert.dates.forEach((date, datei) =>
      formattedDates.push(<div key={"date" + datei}>{formatDate(date)}</div>)
    );
    formattedDates.push(
      <div key={"concert" + concert.title}>{concert.locations[0]}</div>
    );
  } else if (concert.dates.length !== concert.locations.length) {
    console.error("ERROR : The number of dates and locations do not match."); // TODO: Format all data properly when running scripts and throw errors there, not on client end
  } else {
    for (let i = 0; i < concert.dates.length; i++) {
      formattedDates.push(
        <div key={"formatdate" + i}>
          {formatDate(concert.dates[i]) + ", " + concert.locations[i]}
        </div>
      );
    }
  }

  return formattedDates;
}

function Page(state) {
  const meta = state.meta;
  return (
    <div
      className="container mx-auto px-0 md:px-8 max-w-4xl"
      data-aos="fade"
      data-aos-anchor="body"
    >
      <div className="prose prose-content ml-4 md:ml-0">
        {" "}
        {/* Margin is to bump title away from edge on mobile */}
        <h1>Past Seasons</h1>
        <p
          className="text-2xl font-serif"
          style={{ marginTop: "-20px", marginBottom: "1rem" }}
        >
          {meta.year}-{parseInt(meta.year) + 1}
        </p>
      </div>
      <Program meta={meta} />
    </div>
  );
}

export class Program extends Component {
  render() {
    return (
      <>
        {this.props.meta.concerts.map((concert, cindex) => (
          <div key={"concert" + cindex} className="border-2 p-2 my-8">
            <div className="grid grid-cols-1 divide-y">
              <div className="pt-3 pb-12">
                <div className="content-center text-center">
                  <h2 className="text-xl font-medium md:text-2xl">
                    {concert.title}
                  </h2>
                </div>
                <h3 className="text-center text-base">
                  {concert.subtitle?.map((line, i) => {
                    return <div key={"line" + i}>{formatItalics(line)}</div>;
                  })}
                </h3>
                <h3 className="text-sm md:text-base text-right py-2">
                  {getDateLocation(concert)}
                </h3>
                {concert.pieces.map((obj, i) => {
                  return (
                    <div key={"thepiece" + i + concert.title}>
                      <div className="flex pt-6 pb-1 px-2 md:px-8">
                        <div className="text-base md:text-lg lg:text-xl">
                          <div className="2xl:max-w-md lg:max-w-sm md:max-w-xs max-w-xs">
                            {formatItalics(obj.title)}
                          </div>
                        </div>
                        <hr
                          className="flex-1 my-3 md:my-4 overflow-hidden border-gray-300 ml-3"
                          style={{ minWidth: "35px" }}
                        />
                        <div
                          className="text-base md:text-lg lg:text-xl ml-3"
                          style={{ breakAfter: "always" }}
                        >
                          {/*It's currently assumed that composers will never be italicized.
                          It would need to be changed here to italicize composers
                          */}
                          {obj.composer}
                        </div>
                      </div>
                      {obj.extra ? (
                        <div className="pl-6 md:pl-12 text-sm md:text-base col-span-2">
                          {obj.extra.map((line, i2) => (
                            <div
                              key={
                                "concertextra" +
                                i +
                                concert.pieces[i].title +
                                concert.title +
                                i2
                              }
                            >
                              {formatItalics(line)}
                            </div>
                          ))}
                        </div>
                      ) : null}
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        ))}
      </>
    );
  }
}

function formatDate(date) {
  return date.endsWith("d")
    ? // If date string ends with "d" (attached if time is not input)
    new Date(date.slice(0, -1)).toLocaleDateString(undefined, {
      weekday: "short",
      year: "numeric",
      month: "short",
      day: "numeric",
    }) // then return without time
    : new Date(date).toLocaleDateString(undefined, {
      weekday: "short",
      year: "numeric",
      month: "short",
      day: "numeric",
      hour12: true,
      hour: "numeric",
      minute: "2-digit",
    }); // otherwise return with time
}


export default PastSeasonFormat;
