import React, { Component } from "react";
import { ControlledLink } from "../utils/LinkUtils";

export class Textbox extends Component {
  render() {
    return (
      <article className="relative mt-8 py-12 md:py-8 md:mt-16 max-w-5xl mx-auto p-8 bg-gray-100">
        <div className="bg-brownu-link absolute hidden sm:block h-full w-1 left-0 top-0 bottom-0" />
        <h1 className="text-2xl font-bold pb-4">{this.props.data.title}</h1>
        <p className="font-serif text-lg pb-6 leading-relaxed">
          {this.props.data.body}
        </p>
        {this.props.data?.button[0].href && this.props.data?.button[0].text ? (
          <ControlledLink
            href={this.props.data.button[0].href}
            className="p-3 bg-brownu-link hover:bg-black transition-colors duration-200 inline-block text-white font-medium tracking-wide uppercase text-sm"
          >
            {this.props.data.button[0].text /* TODO: Remove hardcoding from [0] to allow for multiple buttons */ }
          </ControlledLink>
        ) : null}
      </article>
    );
  }
}

export default Textbox;
