// This component indiscriminately expects to be in a grid-cols-5 element.

import React, { Component } from "react";

import { NavLink } from "react-router-dom";

export class PageSidebar extends Component {
  constructor(props) {
    super(props);
    this.state = { dropdown: false };
  }
  toggleDropdown = () => {
    this.setState({ dropdown: !this.state.dropdown });
  };
  render() {
    return (
      <>
        <div className="col-span-4 lg:col-span-1 lg:pl-4 lg:pr-8 transition-colors lg:border-r lg:border-gray-200">
          <div
            className="flex items-center lg:hidden py-2 px-4 bg-gray-100"
            onClick={() => this.toggleDropdown()}
          >
            <div
              className={
                "bg-black text-white rounded-full h-8 w-8 flex justify-center transform transition-all duration-200 ease-in-out" +
                (this.state.dropdown ? " -rotate-180" : " rotate-0")
              }
            >
              <svg
                className="mt-1"
                xmlns="http://www.w3.org/2000/svg"
                height="24"
                viewBox="0 0 24 24"
                width="24"
                fill="currentColor"
              >
                <path d="M0 0h24v24H0z" fill="none" />
                <path d="M7 10l5 5 5-5z" />
              </svg>
            </div>
            <div className="flex-grow">
              <div className="text-sm ml-3 text-gray-800 uppercase font-medium tracking-wide font-sans transition-all duration-200 ease-in-out">
                {!this.state.dropdown ? "In This Section" : "Close"}
              </div>
            </div>
          </div>
          <div
            className={"lg:top-0 pt-2 lg:pt-4 overflow-hidden transition-all duration-500 lg:max-h-full" + (this.props.sticky ? " lg:sticky" : "")}
            style={
              this.state.dropdown
                ? {
                  maxHeight: `calc(${this.props.links.length * 45
                    }px + 0.5rem)`,
                }
                : window.innerWidth > 1024
                  ? { maxHeight: "100%" }
                  : { maxHeight: 0 }
            }
          >
            {this.props.links.map((obj, i) => {
              return (
                <Item
                  item={obj}
                  key={i}
                  closeDropdown={() => this.setState({ dropdown: false })}
                />
              );
            })}
          </div>
        </div>
        {/* <div className="col-span-1 block lg:hidden">
          <select>
            {this.props.links.map((obj, i) => {
              return (
                <option value={obj.name} key={"select" + i}>
                  {obj.name}
                </option>
              );
            })}
          </select>
        </div> */}
      </>
    );
  }
}

function Item(props) {
  if (props.item.href.indexOf("://") === -1) {
    return (
      <NavLink
        exact
        to={props.item.href}
        onClick={() => props.closeDropdown()}
        activeClassName="text-brownu-link font-semibold"
        className="font-sans text-brownu-brown hover:text-brownu-link text-sm py-3 px-4 lg:px-2 border-b lg:border-b-0 lg:border-t border-gray-200 block hover:bg-gray-100 duration-100 mx-4 lg:mx-0"
      >
        {props.item.name}
      </NavLink>
    );
  } else {
    return (
      <a
        href={props.item.href}
        className="font-sans text-brownu-brown hover:text-brownu-link text-sm py-3 px-2 border-b lg:border-b-0 lg:border-t border-gray-200 block hover:bg-gray-100 duration-100 mx-4 lg:mx-0"
      >
        {props.item.name}
      </a>
    );
  }
}

export default PageSidebar;
